/**
 * @module Axis
 * This file declares a class that is used to render an axis to add  meaning to
 * plots.
 */
import {
    axisLeft,
    axisBottom,
    axisRight,
    axisTop,
  } from 'd3-axis';
import { Store, mergeRecursive, getSmartComputedStyle, selectElement } from 'muze-utils';
import * as AxisOrientation from '../enums/axis-orientation';
import {
    BAND,
    ORDINAL,
    LINEAR,
    TIME
} from '../enums/scale-type';
import {
    createScale,
} from '../scale-creator';
import { defaultConfig } from './default-config';
import { renderAxis } from '../render-axis';

import './styles.scss';

/**
  * This object is used to associate the axis orientation with the d3 class
  * used to represent that axis.
  */
const axisOrientationMap = {
    [AxisOrientation.LEFT]: axisLeft,
    [AxisOrientation.BOTTOM]: axisBottom,
    [AxisOrientation.RIGHT]: axisRight,
    [AxisOrientation.TOP]: axisTop,
};
 /**
  * This class is used to instantiate a SimpleAxis.
  * @class SimpleAxis
  */
export default class SimpleAxis {
     /**
      * Creates an instance of SimpleAxis.
      * @memberof SimpleAxis
      */
    constructor(config, dependencies) {
        let defaultConfiguration = this.constructor.defaultConfig();

        this.field = config.field;
        this._config = mergeRecursive(defaultConfiguration, config);
        this._computedStyle = getSmartComputedStyle(selectElement('body'), `${this._config.classPrefix}-ticks`);
        this._axisNameStyle = getSmartComputedStyle(selectElement('body'), `${this._config.classPrefix}-axis-name`);

        this._dependencies = dependencies;
        this._mountPoint = null;
        this._range = [];
        this._domain = [];
        this.store = new Store({
            domain: this._domain,
            range: this._range,
            config: this._config,
            mountPoint: this._mountPoint
        });
        this.axisDimensions = {};
        this.rotationLock = false;
        this.scale = this.createScale();
        this.axis = this.createAxis();
    }
    /**
     * Returns the default configuration of simple axis
     *  @return {Object} default configurations
     */
    static defaultConfig () {
        return defaultConfig();
    }
    domain(d) {
        if (d) {
            let scale = this.scale;
            const {
                nice,
                type
            } = this.config();
            if (type === LINEAR && d.length > 0 && d[0] === d[1]) {
                d = [0, +d[0] * 2];
            }
            scale = scale.domain(d);
            if (type !== BAND && this.type !== ORDINAL && nice) {
                scale = scale.nice();
            }
            this._domain = scale.domain();
            this.scale = scale;
            this.store.commit('domain', this._domain);
            return this;
        } return this._domain;
    }
    range(r) {
        if (r) {
            this._range = r;
            this.scale.range(r);
            this.store.commit('range', this._range);
            return this;
        } return this._range;
    }
    mountPoint(mp) {
        if (mp) {
            this._mountPoint = mp;
            return this;
        } return this._mountPoint;
    }
    config(props) {
        if (props) {
            if (props.labels && props.labels.rotation) {
                this.rotationLock = true;
            }
            this._config = mergeRecursive(this._config, props);
            return this;
        } return this._config;
    }
    dependencies() {
        return this._dependencies;
    }
    createScale() {
        const {
            padding,
            type,
            interpolator,
            exponent
        } = this.config();
        const range = this.range();
        let scale = createScale({
            type,
            range,
            interpolator,
            exponent
        });
        if (type === BAND && typeof padding === 'number') {
            scale.padding(padding);
        } else if (type !== BAND && type !== ORDINAL) {
            scale = scale.nice();
        }
        return scale;
    }
    createAxis() {
        const {
            tickFormat,
            orientation
        } = this.config();
        const axisClass = axisOrientationMap[orientation];
        if (axisClass) {
            let axis = axisClass(this.scale);
            if (tickFormat) {
                axis = axis.tickFormat(tickFormat);
            }
            return axis;
        }

        return null;
    }
    /**
      * This method can set the configuration of the rotation of labels (true/false)
      *
      * @memberof SimpleAxis
      */
    setRotationConfig(labelWidth, axisTickLabels) {
        const range = this.range();
        const length = Math.abs(range[0] - range[1]);
        // set multiline config
        if (length > 0 && axisTickLabels.length * labelWidth > length) {
            this.config({ labels: { rotation: 90 } });
        } else {
            this.config({ labels: { rotation: 0 } });
        }
        this.rotationLock = false;
    }
    __computeAxisDimensions() {
        const {
            name,
            labels,
            orientation,
        } = this.config();
        const angle = labels.rotation * Math.PI / 180;

        // set the style on the shared label manager instance
        const {
            labelManager
        } = this.dependencies();
        labelManager.setStyle(this._computedStyle);
        const {
            largestLabelDim,
            axisTickLabels
        } = this.getTickLabelInfo();
        // get the domain of axis
        const domain = this.domain();

        if (domain.length === 0) {
            return null;
        }
        if ((orientation === AxisOrientation.BOTTOM || orientation === AxisOrientation.TOP)
        && this.rotationLock === false) {
            this.setRotationConfig(largestLabelDim.width, this.tickValues || axisTickLabels);
        }

        const tickLabelDim = {
            width: Math.abs(largestLabelDim.height * Math.sin(angle)) +
                        Math.abs(largestLabelDim.width * Math.cos(angle)),
            height: Math.abs(largestLabelDim.width * Math.sin(angle)) +
                        Math.abs(largestLabelDim.height * Math.cos(angle))
        };
        labelManager.setStyle(this._axisNameStyle);
        const axisLabelDim = labelManager.getOriSize(name);
        this.axisDimensions = {
            tickSize: this.getTickSize(),
            tickLabelDim,
            axisLabelDim,
        };
        return this.axisDimensions;
    }
    getTickSize() {
        const {
            showInnerTicks,
            showOuterTicks
         } = this.config();
        const axis = this.axis;
        axis.tickSizeInner(showInnerTicks ? 6 : 0);
        axis.tickSizeOuter(showOuterTicks ? 6 : 0);
        return axis.tickSize();
    }
    /**
     * Gets the space occupied by the parts of an axis
     *
     * @return {Object} object with details about sizes of the axis.
     * @memberof SimpleAxis
     */
    getAxisDimensions() {
        return this.__computeAxisDimensions();
    }
    /**
     * Gets the space occupied by the axis
     *
     * @return {Object} object with details about size of the axis.
     * @memberof SimpleAxis
     */
    getLogicalSpace() {
        // if (!this._logicalSpace) {
        this._logicalSpace = this._calculateLogicalSpace();
        // }
        return this._logicalSpace;
    }

    resetLogicalSpace() {
        this._logicalSpace = null;
    }

    /**
     * Calculates the logical space of the axis
     * @return {Object} Width and height occupied by the axis.
     */
    _calculateLogicalSpace () {
        const range = this.range();

        const {
            axisNamePadding,
            orientation,
            showAxisName
       } = this.config();
        const {
            tickSize,
            tickLabelDim,
            axisLabelDim
        } = this.getAxisDimensions();

        if (orientation === AxisOrientation.LEFT || orientation === AxisOrientation.RIGHT) {
            let height = range.length > 1 ? Math.abs(range[1] - range[0]) : 0;
            if (this.tickValues) {
                const {
                    labelManager
                } = this.dependencies();
                labelManager.setStyle(this._computedStyle);

                height = this.tickValues.reduce((total) => {
                    total += tickLabelDim.height;
                    return total;
                }, 0);
            }
            return {
                height,
                width: tickLabelDim.width + (showAxisName ? axisLabelDim.height : 0) + tickSize + axisNamePadding

            };
        }

        let width = range.length > 1 ? Math.abs(range[1] - range[0]) : 0;
        if (this.tickValues) {
            const {
                labelManager
            } = this.dependencies();
            labelManager.setStyle(this._computedStyle);

            width = this.tickValues.reduce((total) => {
                total += tickLabelDim.width;
                return total;
            }, 0);
        }
        return {
            width,
            height: tickLabelDim.height + (showAxisName ? axisLabelDim.height : 0) + tickSize + axisNamePadding,
        };
    }
    /**
     * Returns the domain of the axes.
     *
     * @return {Array} The domain of the data.
     * @memberof SimpleAxis
     */
    getDomain() {
        return this.domain();
    }
    type() {
        return this.config().type;
    }
    /**
     * Sets the number of ticks for the axis
     *
     * @param {number} numberOfTicks the number of ticks for an axis to be set
     * @memberof SimpleAxis
     */
    setTicks(numberOfTicks) {
        this.numberOfTicks = numberOfTicks;
    }
    /**
     * Sets the label padding for the axis,
     * Label Padding: Padding between axis ticks and axis label
     *
     * @param {number} axisNamePadding the label padding to be set
     * @memberof SimpleAxis
     */
    setaxisNamePadding(axisNamePadding) {
        this.axisNamePadding = axisNamePadding;
    }
    /**
     * Gets the label padding of the axis,
     * Label Padding: Padding between axis ticks and axis label
     *
     * @return {number} axisNamePadding of the axis
     * @memberof SimpleAxis
     */
    getaxisNamePadding() {
        return this.axisNamePadding;
    }
    /**
     * Sets the Mount Point for the axis
     *
     * @param {HTMLElement} mountPoint of the axis
     * @memberof SimpleAxis
     */
    setMountPoint(mountPoint) {
        this.mountPoint = mountPoint;
    }
    /**
     * Gets the Mount Point for the axis
     *
     * @return {number} the Mount Point for the axis
     * @memberof SimpleAxis
     */
    getMountPoint() {
        return this.mountPoint;
    }
    /**
     * This method is used to set the range of the Axis.
     *
     * @param {Array} range The range of the axis.
     * @memberof SimpleAxis
     */
    setRange(range) {
        this.range(range);
    }
    /**
     * Returns the value from the domain when given a value from the range.
     * @param {number} value Value from the range.
     * @return {number} Value
     */
    invert (...value) {
        let values;
        values = value.map(d => this.scale.invert(d)) || [];
        return value.length === 1 ? values[0] : values;
    }
    /**
     * Gets the nearest range value from the given range values.
     * @param {number} v1 Start range value
     * @param {number} v2 End range value
     * @return {Array} range values
     */
    getNearestRange (v1, v2) {
        let p1,
            p2,
            extent,
            range,
            reverse;
        const {
                type
            } = this.config();

        range = this.scale.range();
        reverse = range[0] > range[1];
        if (type === 'band') {
            extent = this.scale.invertExtent(v1, v2);
            p1 = this.scale(reverse ? extent[extent.length - 1] : extent[0]);
            p2 = this.scale(reverse ? extent[0] : extent[extent.length - 1]) + this.scale.bandwidth();
            return [p1, p2];
        }
        return [v1, v2];
    }

     /**
      * This method is used to assign a domain to the ax is.
      *
      * @param {Array} domain the domain of the scale
      * @memberof SimpleAxis
      */
    updateDomain(domain) {
        return this.domain(domain);
    }

     /**
      * This method is used to assign a domain to the axis.
      *
      * @param {Array} domain the domain of the scale
      * @memberof SimpleAxis
      */
    updateDomainBounds(domain) {
        const {
            type
        } = this.config();
        let currentDomain = this.domain();
        if (currentDomain.length === 0) {
            currentDomain = domain;
        }
        if (type === BAND) {
            currentDomain = currentDomain.concat(domain);
        }
        else {
            currentDomain = [Math.min(currentDomain[0], domain[0]), Math.max(currentDomain[1], domain[1])];
        }
        this.domain(currentDomain);
    }

    setTickValues(tickValues) {
        this.tickValues = tickValues;

        if (tickValues instanceof Array) {
            this.axis.tickValues(tickValues);
            return this;
        }
        this.getTickValues();
        return this;
    }

    getTickValues() {
        const {
            type,
            orientation,
        } = this.config();
        const range = this.range();
        const axis = this.axis;
        let ticks;
        if (type === LINEAR) {
            ticks = axis.scale().ticks();
            const availableSpace = Math.abs(range[0] - range[1]);
            const labelProps = this.getTickLabelInfo().largestLabelDim;
            let numberOfValues;
            if (!this.tickValues) {
                numberOfValues = ticks.length;
                let labelDim = labelProps.height;
                if (orientation === AxisOrientation.BOTTOM || orientation === AxisOrientation.TOP) {
                    labelDim = labelProps.width;
                }
                if (ticks.length * (labelDim * 1.25) > availableSpace) {
                    numberOfValues = Math.floor(availableSpace / (labelDim * 1.25));
                }
            }
            if (this.tickValues) {
                numberOfValues = this.tickValues;
            }
            if (numberOfValues < 2) {
                numberOfValues = 2;
            }
            axis.tickValues(axis.scale().ticks(numberOfValues));
            return axis.tickValues();
        }
        else if (type === TIME) {
            return this.scale.ticks();
        }
        return axis.scale().domain();
    }
    /**
      * This method returns the information of the tick labels:
      * List of tick labels, largest label and its dimensions
      *
      * @return {Object} information of the tick labels
      * @memberof SimpleAxis
      */
    getTickLabelInfo() {
        const {
            scale
        } = this,
            labelFunc = scale.ticks || scale.quantile || scale.domain,
            { labelManager } = this._dependencies,
            { tickFormat } = this.config(),
                       // get the values along the domain
            axisTickLabels = this.tickValues || labelFunc();

        let largestLabel = '',
            labelProps,
            allLabelLengths = [],
            tickLabels = axisTickLabels.map((originalLabel, i) => {
            // convert to string for quant values
                const label = tickFormat ? tickFormat(originalLabel) :
                 (scale.tickFormat ? scale.tickFormat()(originalLabel) : originalLabel),
                    temp = label.toString();
                allLabelLengths.push(labelManager.getOriSize(temp));
                if (temp.length > largestLabel.length) {
                    largestLabel = temp;
                    labelProps = allLabelLengths[i];
                }

                return label;
            });
        labelProps = labelManager.getOriSize(largestLabel);
        return { largestLabel, largestLabelDim: labelProps, axisTickLabels: tickLabels, allLabelLengths };
    }

    /**
     * This method returns the width in pixels for one
     * unit along the axis. It is only applicable to band scale
     * and returns undefined for other scale type.
     *
     * @return {number} the width of one band along band scale axis
     * @memberof SimpleAxis
     */
    getUnitWidth() {
        const {
            scale
        } = this;
        if (scale.bandwidth) {
            return scale.bandwidth();
        }
        return undefined;
    }
    /**
     * This method returns an object that can be used to
     * reconstruct this instance.
     *
     * @return {Object} the serializable props of axis
     * @memberof SimpleAxis
     */
    serialize() {
        return {
            name: this.name,
            type: this.type,
            range: this._range,
            orientation: this.orientation,
            style: this.style,
        };
    }
    /**
     * Returns the id of the axis.
     * @return {string} Unique identifier of the axis.
     */
    id() {
        return this.config().id;
    }
    /**
     * Listener attached to the axis on change of parameters.
     *
     * @param {Function} callback to be excuted on change of domain range etc
     * @memberof SimpleAxis
     */
    _registerChangeListeners(callback) {
        let store = this.store;
        store.registerImmediateListener(['domain', 'range', 'config'], (...params) => {
            this.render();
            callback && callback(...params);
        });
    }

       /**
     * This method is used to render the axis inside
     * the supplied svg container.
     *
     * @param {SVGElement} svg the svg element in which to render the path
     * @memberof SimpleAxis
     */
    /* istanbul ignore next */render(svg) {
        const config = this.config();
        let {
            orientation,
            xOffset,
            yOffset
        } = config;
        if (svg) {
            this.mountPoint(svg);
        }
        if (this.mountPoint()) {
            let logicalSpace = this._logicalSpace;

            if (orientation === AxisOrientation.LEFT) {
                xOffset = xOffset === undefined ? logicalSpace.width : xOffset;
            }

            if (orientation === AxisOrientation.TOP) {
                yOffset = yOffset === undefined ? logicalSpace.height : yOffset;
            }
            xOffset = xOffset || 0;
            yOffset = yOffset || 0;
            this.config({ xOffset, yOffset });


            renderAxis(this);
        }
    }
    remove () {
        this.store.unsubscribeAll();
        selectElement(this.mountPoint()).remove();
        return this;
    }

    unsubscribe() {
        this.store.unsubscribeAll();
    }

    isReverse () {
        let range = this.scale.range();
        return range[0] > range[1];
    }

    getPvr () {
        let scale = this.scale,
            range = scale.range(),
            domain = scale.domain();

        return Math.abs(range[1] - range[0]) / (domain[1] - domain[0]);
    }
 }
