/**
 * This file exports functionality that is used to render axis.
 */
 import {
    selectElement,
    makeElement,
    getQualifiedClassName
} from 'muze-utils';

 import * as AxisOrientation from './enums/axis-orientation';

 function toRad(angle) {
     return angle * Math.PI / 180;
 }

 const rotateAxis = (instance, container, orientation, rotation, labelManager, fixedBaseline) => {
     const scale = instance.scale;

     container.selectAll('text').each(function(d, i) {
         const temp = scale.tickFormat ? scale.tickFormat()(d) : d;
         d = temp.toString();

         let tickLabelDim = labelManager.getOriSize(d);
         const width = tickLabelDim.width * 0.5;
         const height = tickLabelDim.height * 0.5;
         if (rotation < 0) {
             rotation = 360 + rotation;
         }
         const quadrant = 4 - Math.floor(rotation / 90);

         const rotationNormalizer = ((quadrant % 2 === 0) ? rotation :
                             180 * Math.ceil(rotation / 180) - rotation) % 180;
         let yShift = Math.sqrt(height ** 2 + width ** 2) * Math.sin(toRad(rotationNormalizer));
         if ((quadrant === 3 || quadrant === 2) && !(rotationNormalizer > 67.5 && rotationNormalizer <= 90)) {
             yShift += height * 2;
         }
         let xShift = width;

         if (rotation === 90)
             { xShift = height; }
         if (rotation === 270)
             { xShift = -height; }
         xShift = (i === 0 && fixedBaseline) ? xShift * 2 : xShift;
         selectElement(this)
                         .attr('transform', `translate(${xShift} ${orientation ===
                     AxisOrientation.TOP ? -yShift : yShift}) rotate(${rotation})`);
     });
 };
 /**
  * This method is used to render the axis inside an
  * svg container.
  *
  * @export
  * @param {Object} axisInstance the nput object required to render axis
  * @param {string} axisInstance.orientation the orientation of axis
  * @param {Object} axisInstance.scale instance of d3 scale
  * @param {SVGElement} axisInstance.container the container in which to render
  */
 export function renderAxis(axisInstance) {
     const config = axisInstance.config();
     const labelManager = axisInstance.dependencies().labelManager;
     const mountPoint = axisInstance.mountPoint();
     const range = axisInstance.range();
     let {
        axis,
        _axisNameStyle
     } = axisInstance;
     const {
        orientation,
        name,
        xOffset,
        yOffset,
        style,
        labels,
        axisNamePadding,
        className,
        axisName,
        fixedBaseline,
        showAxisName,
        type,
        show,
        id,
        classPrefix,
     } = config;
     const {
         rotation
     } = labels;
     if (!show) {
         return;
     }
     let
         textNode,
         labelOffset,
         labelProps,
         tickSize = axis.tickSize(),
         labelPropsHeight,
         selectContainer = makeElement(selectElement(mountPoint), 'g', [1], `${className}`),
         axisNameClass = axisName.defClassName,
         axisNameQualfiedClass = getQualifiedClassName(axisNameClass, id, classPrefix);
     labelManager.setStyle(style);
     if (!axisInstance.tickValues) {
         axisInstance.getTickValues();
     }
        // the length of axis
     const availableSpace = Math.abs(range[0] - range[1]);

     labelProps = axisInstance.axisDimensions.tickLabelDim;
     labelPropsHeight = labelProps.height;

     selectContainer.attr('transform', `translate(${xOffset},${yOffset})`)
                     .call(axis);

     // rotate labels if not enough space is available

     if (rotation !== 0) {
         rotateAxis(axisInstance, selectContainer, orientation, rotation, labelManager, fixedBaseline);
     } else {
         selectContainer.selectAll('text').attr('transform', '');
     }
     textNode = selectContainer.select(`.${axisNameQualfiedClass[1]}`);
     if (textNode.empty()) { // append if not present
         textNode = selectContainer.append('text');
     }
     textNode.attr('text-anchor', 'middle')
                     .attr('class', axisNameQualfiedClass.join(' '))
                     .text(() => name);

     // apply style to text node
     Object.keys(style).forEach((key) => {
         textNode.style(key, style[key]);
     });
     const tickText = selectContainer.selectAll('.tick text')
                     .classed(`${classPrefix}-ticks`, true);


     if (fixedBaseline) {
         tickText.style('transform', (d, i) => {
             const { width: shiftWidth, height: shiftHeight } = labelManager.getOriSize(d);
             if (i === 0 && (orientation === 'left' || orientation === 'right') && type === 'linear') {
                 return `translate(0, -${(shiftHeight) / 3}px)`;
             }
             if (i === 0 && (orientation === 'top' || orientation === 'bottom')
             && type === 'linear' && rotation === 0) {
                 return `translate(${shiftWidth / 2}px,  ${0}px) rotate(${rotation}deg)`;
             }
         });
     }

    // render labels based on orientation of axis
     labelOffset = availableSpace / 2;
     textNode.classed('axisName', true);
     textNode.classed('hidden', !showAxisName);

     labelManager.setStyle(_axisNameStyle);
     const axisDim = labelManager.getOriSize(name).height;
     switch (orientation) {
     case AxisOrientation.LEFT:
         textNode.attr('transform',
        `translate(${-(labelProps.width + tickSize + axisNamePadding + axisDim)},${yOffset + labelOffset})rotate(-90)`);
         break;
     case AxisOrientation.RIGHT:
         textNode.attr('transform',
         `translate(${(tickSize + labelProps.width + axisNamePadding + axisDim)},${yOffset + labelOffset})rotate(-90)`);
         break;
     case AxisOrientation.TOP:
         textNode.attr('transform',
         `translate(${labelOffset + xOffset},${-((labelPropsHeight) + tickSize + axisNamePadding + axisDim)})`);
         break;
     case AxisOrientation.BOTTOM:
         textNode.attr('transform',
         `translate(${labelOffset + xOffset},${((labelPropsHeight) + tickSize + axisNamePadding + axisDim)})`);
         break;
     default:
     }
 }
