/**
 * @module Axis
 * This file declares a class that is used to render an axis to add  meaning to
 * plots.
 */
import { symbol } from 'd3-shape';
import { getUniqueId, symbolFns } from 'muze-utils';
import {
    createScale
} from '../scale-creator';

/**
* This class is used to instantiate a SimpleAxis.
* @class SimpleAxis
*/
export default class ShapeAxis {
   /**
    * Creates an instance of SimpleAxis.
    * @param {Object} params input parameters.
    * @param {Object | undefined} params.range Type of color scheme.
    * @param {string} params.type The type of scale to handle.
    * @memberof ShapeAxis
    */
    constructor(params) {
        this._id = params.id || getUniqueId();
        this.type = 'ordinal';
        this.range = params.range || ['circle', 'diamond', 'star', 'cross', 'square', 'wye', 'triangle'];
        this.scale = createScale({
            type: 'ordinal',
            range: this.range
        });
        this.generator = params.generator;
    }

    getShape (value) {
        if (this.generatedShapes) {
            return this.generatedShapes[value];
        }
        let shapeType = this.scale(value);
        if (shapeType === 'string') {
            return symbol().type(symbolFns[shapeType]);
        } return shapeType;
    }

    /**
     * This function is used to update the parameters of this instance.
     *
     * @param {Object} params The input params.
     * @param {Object | undefined} params.scheme Type of color scheme.
     * @param {string | undefined} params.name the label to show on axis.
     * @param {string | undefined} params.type The type of scale to handle.
     * @return {ShapeAxis} Instance of color axis.
     * @memberof ShapeAxis
     */
    update(params) {
        Object.keys(params).forEach((key) => {
            this[key] = params[key];
        });
        return this;
    }

   /**
    * This method is used to assign a domain to the axis.
    *
    * @param {Array} domain the domain of the scale
    * @memberof ShapeAxis
    */
    updateDomain(domain) {
      /* istanbul ignore next */
        this.uniqueVals = domain;
        this.scale.domain(domain);
        if (this.generator) {
            this.generatedShapes = {};
            domain.forEach((value) => {
                this.generatedShapes[value] = this.generator(value);
            });
        }
    }

    /**
     * This method returns an object that can be used to
     * reconstruct this instance.
     *
     * @return {Object} the serializable props of axis
     * @memberof ShapeAxis
     */
    serialize() {
        return {
            name: this.name,
            type: this.type,
            range: this.range,
        };
    }

    /**
     * Returns the id of the axis.
     * @return {string} Unique identifier of the axis.
     */
    id() {
        return this._id;
    }
}
