/**
 * @module Axis
 * This file declares a class that is used to render an axis to add  meaning to
 * plots.
 */
import {
    interpolateArray
} from 'muze-utils';
import {
    SCHEME2,
    SCHEME1,
    SCHEME3
} from '../enums/color-scheme';
import {
    createScale,
    getScheme
} from '../scale-creator';

const colors = ['#F44336', '#E91E63', '#9C27B0', '#673AB7', '#3F51B5', '#2196F3', '#03A9F4', '#00BCD4', '#009688',
    '#4CAF50', '#8BC34A', '#CDDC39', '#FFEB3B', '#FFC107', '#FF9800', '#FF5722', '#795548', '#9E9E9E', '#607D8B'];
/**
* This class is used to instantiate a SimpleAxis.
* @class SimpleAxis
*/
export default class ColorAxis {
   /**
    * Creates an instance of SimpleAxis.
    * @param {Object} params input parameters.
    * @param {Object | undefined} params.scheme Type of color scheme.
    * @param {string} params.name the label to show on axis.
    * @param {string} params.type The type of scale to handle.
    * @memberof ColorAxis
    */
    constructor(params) {
        this._id = params.id;
        if ('interpolate' in params) {
            this.interpolate = params.interpolate;
        } else {
            this.interpolate = true;
        }

        this.type = params.type || 'ordinal';
        this.scheme = params.scheme || 'interpolateRainbow';
        this.schemeType = 'ordinal';
        this.getColorScheme(this.scheme);
        this.updateDomain(params.domain);
    }

    getColor(domainVal) {
        if (this.schemeType === 'sequential') {
            let numVal = 0;
            if (this.type === 'ordinal') {
                numVal = this._domain[this.uniqueVals.indexOf(domainVal)];
            } else {
                numVal = (domainVal - this._domain[0]) / (this._domain[this._domain.length - 1] - this._domain[0]);
            }
            return this.scale(numVal);
        }
        return this.scale(domainVal);
    }
    getColorScheme(scheme) {
        if (scheme && typeof (scheme) === 'string') {
            this.scale = getScheme(scheme);
            this.scheme = scheme;
            this.schemeType = scheme[0] === 'i' ? 'sequential' : 'ordinal';
        } else {
            this.scheme = scheme || [...colors, ...SCHEME1, ...SCHEME2, ...SCHEME3];
            this.schemeType = this.interpolate ? 'sequential' : 'ordinal';
            if (this.interpolate) {
                this.scale = createScale({
                    type: 'linear',
                    range: this.scheme,
                });
                return this.scheme;
            }
            if (this.type === 'ordinal') {
                this.scale = createScale({
                    type: 'ordinal',
                    range: this.scheme,
                });
            }
            else {
                this.scale = createScale({
                    type: 'quantize',
                    range: this.scheme,
                });
            }
        }
        return this.scheme;
    }
    /**
     * This function is used to update the parameters of this instance.
     *
     * @param {Object} params The input params.
     * @param {Object | undefined} params.scheme Type of color scheme.
     * @param {string | undefined} params.name the label to show on axis.
     * @param {string | undefined} params.type The type of scale to handle.
     * @return {ColorAxis} Instance of color axis.
     * @memberof ColorAxis
     */
    update(params) {
        Object.keys(params).forEach((key) => {
            this[key] = params[key];
        });
        if (params.scheme) {
            this.getColorScheme(params.scheme);
        }
        return this;
    }

    /**
     * This method is used to update the name of the Axis.
     *
     * @param {Object} scheme The color scheme tp use.
     * @memberof ColorAxis
     */
    setColorScheme(scheme) {
        this.scheme = scheme;
        this.scale = createScale({
            type: this.type,
            range: scheme
        });
    }
   /**
    * This method is used to assign a domain to the axis.
    *
    * @param {Array} domain the domain of the scale
    * @memberof ColorAxis
    */
    updateDomain(domain = []) {
      /* istanbul ignore next */
        if (this.type === 'linear' && this.scale.range) {
            this.uniqueVals = interpolateArray(domain, this.scale.range().length);
        } else {
            this.uniqueVals = domain;
        }

        // If ordinal scale, convert to indexed domain
        let indexedDomain = [];
        if (this.type === 'ordinal') {
            indexedDomain = domain.map((d, i) => (i + 1) / domain.length);
            domain = indexedDomain;
        }


        this._domain = domain;
        if (this.schemeType === 'sequential') {
            if (this.scale.range) {
                this.scale.domain(interpolateArray([0.1, 1], this.scale.range().length));
            } else {
                this.scale.domain([0.1, 1]);
            }
        }
        else {
            this.scale.domain(this._domain);
        }
    }
  /**
   * This method returns an object that can be used to
   * reconstruct this instance.
   *
   * @return {Object} the serializable props of axis
   * @memberof ColorAxis
   */
    serialize() {
        return {
            name: this.name,
            type: this.type,
            scheme: this.scheme,
        };
    }

    /**
     * Returns the id of the axis.
     * @return {string} Unique identifier of the axis.
     */
    id() {
        return this._id;
    }
}
