/**
 * @module Axis
 * This file declares a class that is used to render an axis to add  meaning to
 * plots.
 */
import {
    interpolateArray
} from 'muze-utils';
import {
    createScale,
} from '../scale-creator';


/**
* This class is used to instantiate a SimpleAxis.
* @class SimpleAxis
*/
export default class SizeAxis {
   /**
    * Creates an instance of SimpleAxis.
    * @param {Object} params input parameters.
    * @param {string} params.name the label to show on axis.
    * @param {string} params.type The type of scale to handle.
    * @memberof SizeAxis
    */
    constructor(params) {
        this._id = params.id;
        this.interpolate = params.interpolate || true;
        this.type = params.type || 'ordinal';
        this.schemeType = 'ordinal';
        this.range = params.range || [10, 100];
        this.scale = createScale({
            type: this.interpolate ? this.type : 'quantile',
            range: this.range || [],
        });
        this.scaleFactor = params.scaleFactor || 100;
        this.updateDomain(params.domain);
    }

    getComputedRange(rangeValues) {
        if ((this.type === 'ordinal' || this.interpolate) && this._domain) {
            const domainLength = this._domain.length;
            return interpolateArray(rangeValues, domainLength);
        }
        return rangeValues;
    }

    getSize(domainVal) {
        if (this._domain[0] === this._domain[1]) {
            return 1;
        }
        if (!this.interpolate && this.type !== 'ordinal') {
            const rangeVal = this.scale(domainVal);
            return rangeVal / this.range[this.range.length - 1];
        }
        return this.scale(domainVal) / this.range[this.range.length - 1];
    }
    /**
     * This function is used to update the parameters of this instance.
     *
     * @param {Object} params The input params.
     * @param {string | undefined} params.name the label to show on axis.
     * @param {string | undefined} params.type The type of scale to handle.
     * @return {SizeAxis} Instance of color axis.
     * @memberof SizeAxis
     */
    update(params) {
        Object.keys(params).forEach((key) => {
            this[key] = params[key];
        });
        if (params.type || params.scheme) {
            this.scale = createScale({
                type: this.type,
                range: this.scheme,
            });
        }
        return this;
    }

   /**
    * This method is used to assign a domain to the axis.
    *
    * @param {Array} domain the domain of the scale
    * @memberof SizeAxis
    */
    updateDomain(domain) {
      /* istanbul ignore next */
        this.uniqueVals = domain;
        if (domain) {
            this._domain = domain;
            this.scale.domain(domain);
        }
        this.updateRange();
        return this;
    }
    setScaleFactor(sf) {
        this.scaleFactor = sf;
    }
    getScaleFactor() {
        return this.scaleFactor;
    }

    updateRange (range) {
        const updatedRange = range || this.range;
        this.range = this.getComputedRange(updatedRange);
        this.scale.range(this.range);
        this.rangeSum = this.range.reduce((total, num) => {
            total += num;
            return total;
        }, 0);
    }
    /**
     * This method returns an object that can be used to
     * reconstruct this instance.
     *
     * @return {Object} the serializable props of axis
     * @memberof SizeAxis
     */
    serialize() {
        return {
            name: this.name,
            type: this.type,
            scheme: this.scheme,
        };
    }

    /**
     * Returns the id of the axis.
     * @return {string} Unique identifier of the axis.
     */
    id() {
        return this._id;
    }
}
